.footer {
  background-color: #1e2732;
  bottom: 0;
}

.footer .footerSection {
  margin-top: 40px;
  margin-bottom: 15px;
  float: left;
  list-style-type: none;
  word-break: keep-all;
}

.footer .footerSection ul {
  list-style-type: none;
}

.footer .footerSection li {
  color: #fff;
  font-family: Lato;
  font-size: 14px;
  font-weight: bold;
  padding: 5px;
}

.footer .footerSection .logoSection {
  margin-top: 20px;
}

@media screen and (min-width: 480px) {
  .footer .footerSection .logoSection .logo {
    display: inline-block;
    margin-bottom: 10px;
  }
}

.footer .footerSection .logoSection .logo {
  padding: 14px 15px;
  border: solid 1px #ffffff;
  border-radius: 25px;
  margin-right: 20px;
}

.footer .footerSection .logoSection .logo i {
  font-size: 16px;
  font-weight: 900;
}

.copyrightSection {
  background-color: #1a222b;
  text-align: center;
}

.copyrightSection .copyrightContent {
  font-family: Lato;
  font-size: 14px;
  font-weight: bold;
  color: #fff;
  margin: 20px;
}

.copyrightSection .copyrightContent .textStyle1 {
  color: #fc4349;
  padding: 5px;
}

.copyrightSection .copyrightContent .textStyle2 {
  color: #749ec7;
  padding: 5px;
}



